import React, { Component } from 'react';
import { Button, Card, CardBody, CardHeader, Col, Row } from 'reactstrap';
import StarRatings from 'react-star-ratings';

const books = require('../../../assets/data/books.json');

class BookDetails extends Component {
  constructor(props) {
    super(props);

    this.state = { booksData: {}, rating: 0 };
    this.changeRating = this.changeRating.bind(this);
  }

  componentDidMount() {
    const id = Number(this.props.location.search.replace('?id=', ''));
    const data = books.find(item => {
      return item.id === id;
    });
    this.setState({ bookData: data });
  }

  changeRating(newRating, name) {
    this.setState({
      rating: newRating
    });
  }

  render() {
    //     authors: "Stephenie Meyer"
    // average_rating: 3.57
    // best_book_id: 41865
    // book_id: 41865
    // books_count: 226
    // id: 3
    // image_url: "https://images.gr-assets.com/books/1361039443m/41865.jpg"
    // isbn: 316015849
    // isbn13: 9780316015840
    // language_code: "en-US"
    // original_publication_year: 2005
    // original_title: "Twilight"
    // ratings_1: 456191
    // ratings_2: 436802
    // ratings_3: 793319
    // ratings_4: 875073
    // ratings_5: 1355439
    // ratings_count: 3866839
    // small_image_url: "https://images.gr-assets.com/books/1361039443s/41865.jpg"
    // title: "Twilight (Twilight, #1)"
    // work_id: 3212258
    // work_ratings_count: 3916824
    // work_text_reviews_count: 95009
    // __proto__: Object

    const { bookData } = this.state;
    return (
      <div className="animated fadeIn">
        <Row>
          <h1>Book Details</h1>
        </Row>
        {bookData && bookData.id ? (
          <Row>
            <Col md="3" sm="4" xs="4">
              <img style={{ width: '100%', height: 'auto' }} src={bookData.image_url}></img>
            </Col>
            <Col className="p-5" md="6">
              <Row className="pb-5">
                <h2>{bookData.title}</h2>
              </Row>
              <Row>
                <h4>Authors : </h4>
                <h4>{bookData.authors}</h4>
              </Row>
              <Row>
                <h4>Publication Year : </h4>
                <h4>{bookData.original_publication_year}</h4>
              </Row>
              <Row className="pb-5">
                <h4>ISBN: </h4>
                <h4>{bookData.isbn}</h4>
              </Row>
              <Row>
                <Col md="6">
                  {' '}
                  <StarRatings
                    rating={this.state.rating}
                    starRatedColor="blue"
                    changeRating={this.changeRating}
                    numberOfStars={5}
                    name="rating"
                    starDimension="30px"
                  />
                </Col>
                <Col md="6">
                  <Button
                    type="button"
                    className="btn btn-warning"
                    style={{ fontSize: '2em', maxWidth: '5em', width: '5em' }}
                  >
                    Buy
                  </Button>
                </Col>
              </Row>
              <Row className="text-center">
                <Col md="4">
                  {' '}
                  <span>Rating : {this.state.rating}</span>
                </Col>
                <Col md="8">
                  {' '}
                  <span>&nbsp;</span>
                </Col>
              </Row>
            </Col>
          </Row>
        ) : (
          ''
        )}
      </div>
    );
  }
}

export default BookDetails;
